import React from "react";
const { GatsbyImage, getImage } = require("gatsby-plugin-image");

// if cover is empty its value is null; if videoCover is empty its value is empty array
const ArtworkCard = ({ photo = {} }) => {
  const linkedImage =
    photo.gallery?.length > 0
      ? {
          url: photo.gallery[0].image.asset.url,
          width: photo.gallery[0].image.asset.width,
          height: photo.gallery[0].image.asset.height,
        }
      : null;

  return (
    <figure
      className={`grid-item ${photo.columnX2 ? "grid-item--width2" : ""} col-6 col-md-4`}
      itemProp="associatedMedia"
      itemScope
      itemType="http://schema.org/ImageObject"
    >
      <a
        href={linkedImage ? linkedImage.url : "/"}
        data-pswp-width={linkedImage ? linkedImage.width : ""}
        data-pswp-height={linkedImage ? linkedImage.height : ""}
        target="_blank"
        rel="noreferrer"
        itemProp="contentUrl"
      >
        {photo.videoCover?.length > 0 ? (
          <video
            poster={photo.cover?.asset?.url}
            width={photo.cover?.asset?.width}
            height={photo.cover?.asset?.height}
            playsInline
            autoPlay
            muted
            loop
          >
            {photo.videoCover.map((vid) => (
              <source key={vid._key} src={vid.asset.url} />
            ))}
          </video>
        ) : (
          <GatsbyImage
            className="img-fluid"
            image={getImage(photo.cover?.asset)}
            alt="photo"
          />
        )}
      </a>
      {photo.gallery?.length > 1 && photo.gallery.slice(1).map((galleryItem) => (
        <a
          key={galleryItem._key}
          href={galleryItem.asset.url}
          className="d-none"
          data-pswp-width={galleryItem.asset.width}
          data-pswp-height={galleryItem.asset.height}
        >
          {galleryItem._key}
        </a>
      ))}
    </figure>
  );
};

export default ArtworkCard;
