import React, {useEffect, useRef} from "react";
import PhotoSwipeLightbox from "photoswipe/lightbox";
import { graphql } from "gatsby";
import { allSanityTypeItems } from "../../utils/utils";
import Layout from "../../components/layout/layout.component";
import ArtworkCard from "../../components/cards/artwork-card/artwork-card.component";
import Seo from "../../components/seo/seo.component";
import Masonry from "masonry-layout";
import imagesloaded from "imagesloaded";
import "photoswipe/style.css";

const ArtworkPage = ({ data }) => {
  const ref = useRef();

  useEffect(() => {
    if (typeof Masonry === "undefined" && !!ref.current) {
      console.log("ERROR: Masonry is not available. Fallback to bootstrap layout");
      ref.current.classList.add("row", "justify-content-center");
      return;
    }

    const imgLoad = imagesloaded(ref.current);

    imgLoad.on('done', () => {
      // console.log("[WARN] all images are loaded");
      new Masonry(".gallery", {
        itemSelector: ".grid-item", // The grid item selector
        columnWidth: ".grid-item", // The column width selector
        gutter: 15, // Space between grid items
        horizontalOrder: true, // Enable horizontal ordering
        fitWidth: true,
      });
    });
    // Cleanup function
    return () => {
      imgLoad.off('done');
    };
  }, []);

  useEffect(() => {
    let lightbox = new PhotoSwipeLightbox({
      gallery: "#gallery",
      children: "a",
      pswpModule: () => import("photoswipe"),
    });
    lightbox.init();

    return () => {
      lightbox.destroy();
      lightbox = null;
    };
  }, []);

  const photos = allSanityTypeItems(data, "Artwork");

  return (
    <Layout>
      <main className="main container-fluid p-0">
        {photos?.length > 0 ? (
          <div id="gallery" ref={ref} className="gallery pt-3 pb-5 pswp-gallery">
            {photos.map((photo) => (
              <ArtworkCard key={photo._id} photo={photo} />
            ))}
          </div>
        ) : (
          <h2>...work in progress</h2>
        )}
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    allSanityArtwork(sort: { orderRank: ASC }) {
      edges {
        node {
          _id
          title
          columnX2
          cover {
            asset {
              height
              width
              url
              gatsbyImageData(width: 695, placeholder: BLURRED)
            }
          }
          gallery {
            image {
              asset {
                url
                width
                height
              }
            }
          }
        }
      }
    }
  }
`;

export default ArtworkPage;

export const Head = () => {
  return (
    <>
      <Seo pathname="artwork" />
      <body className="artwork" />
    </>
  );
};
